



























































































export default{
	data(){
		return{
			iswhole: false, //支付和充值显示
			isrecharge: true, //单独的充值隐藏
			ispayment: true,  //单独的支付隐藏
			PayAndPayList: '',  //全部列表
			RechargeList: '',  //充值列表
			PaymentList: '', //余额列表
			totalBalance: '',  //全部类别里的：余额
			totalRecharge: '',  //充值类别里的：总充值
			totalPayment: '',  //支付类别里的： 总支付
			checkNum: 0,
			item:[{
				value:'one',
				name:'全部',
			},
			{
				value:'one',
				name:'充值',
			},
			{
				value:'one',
				name:'支付',
			}]
		}
	},
	methods:{
		//根据支付方式显示对应的图标；name: 支付方式名
		iconFor(name) {
			var classInof = null;
			switch(name) {
				case '微信':
					classInof = '../../static/image/weixin.png';
					break;
				case '支付宝':
					classInof = '../../static/image/zhifu.png';
					break;
				case '现金':
					classInof = '../../static/image/xianjin.png';
					break;
				case '余额':
					classInof = '../../static/image/yue.png';
					break;
			}
			return classInof;
		},
		// 根据支付方式 显示对应提示
		mode(ban){
			var balance = null;
			if(ban == null){
				balance = '支付'
				return balance;
			}else{
				balance = '充值'
				return balance;
			}	
		},		
		// 切换按钮状态
		billBtn(index){
			this.checkNum = index;
			// 全部
			if(index == 0){
				this.iswhole = false
				this.isrecharge = true
				this.ispayment = true
			}else if(index == 1){  //充值
				this.iswhole = true;
				this.isrecharge = false;
				this.ispayment = true;
				var wulala=uni.getStorageSync("jsonList");  //获取存储在Storage里的值大家都在发
				var data = JSON.parse(wulala); //JSON字符串转对象
				var Signboard = data.Ident_Signboard;  //给Signboard 赋值最近更新
				var Signguid = data.Ident_Signguid;  //给Signguid 赋值
				uni.request({
					url: 'http://192.168.199.194/mobile/personal/getRecharge',
					method: 'GET',
					data:{
						Ident_Signboard: Signboard,
						Ident_Signguid: Signguid
					},
					success: (res) => {
						if(res.data.status == 0){
							this.RechargeList = res.data.recharge.data
						}
					}
				})
			}else{  //支付
				this.iswhole = true
				this.isrecharge = true
				this.ispayment = false
				var wulala=uni.getStorageSync("jsonList");  //获取存储在Storage里的值大家都在发
				var data = JSON.parse(wulala); //JSON字符串转对象
				var Signboard = data.Ident_Signboard;  //给Signboard 赋值最近更新
				var Signguid = data.Ident_Signguid;  //给Signguid 赋值
				uni.request({
					url: 'http://192.168.199.194/mobile/personal/getPayRecord',
					method: 'GET',
					data:{
						Ident_Signboard: Signboard,
						Ident_Signguid: Signguid
					},
					success: (res) => {
						if(res.data.status == 0){
							this.PaymentList = res.data.payfor.data
						}
					}
				})
			}
		}	
	},
	onLoad:function(){
		var wulala=uni.getStorageSync("jsonList");  //获取存储在Storage里的值大家都在发
		var data = JSON.parse(wulala); //JSON字符串转对象
		var Signboard = data.Ident_Signboard;  //给Signboard 赋值最近更新
		var Signguid = data.Ident_Signguid;  //给Signguid 赋值
		uni.request({
			url: 'http://192.168.199.194/mobile/personal/PayAndPayFor',
			method: 'GET',
			data:{
				Ident_Signboard: Signboard,
				Ident_Signguid: Signguid
			},
			success: (res) => {
				if(res.data.status == 0){
					this.PayAndPayList = res.data.data;  //全部列表：赋值
					this.totalRecharge = (res.data.totalPay).toFixed(2);  //充值类别里的：总充值
					this.totalPayment = (res.data.totalPayment).toFixed(2);  //支付类别里的：总支付
					this.totalBalance = (parseFloat(res.data.totalPay) - parseFloat(res.data.totalPayment)).toFixed(2);  //全部类别里的：余额
				}
			}
		});
	}
}
